import React from 'react'
import { connect } from 'react-redux'
import { addTag } from  '.tags/actions'

class TaskTags extends React.Component{
	constructor(){
		super()

		this.state = {
			isOn:false,
			tags:['Urgent', 'Internal','Design','Add Functionality' ]
		}
	}

	addTag(e) {
		e.preventDefault()
		this.props.addTag(e.target.value)
	}

	render(){
		return (
			<ul>
				{this.state.tags.map((tag,i) => {
					return (
						<li key={i}>
							<button name={tag} value={tag} onClick={this.addTag}>{tag} </button>
						</li>
					)
				})}
			</ul>
		)
	}


}

export default connect(
	state => ({

	}), {
		addTag,
	}
)(TaskTags)



